import './index.css'

import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faCode, faStar, faCodeBranch} from '@fortawesome/free-solid-svg-icons'
import React from "react";


function Repository (props) {

    const {name, description, language, stargazers_count, forks} = props

    return (
        <article className="Repository">
            <span className="Repository-name">{name}</span>
            <span className="Repository-description">{description}</span>
            <div className="Repository-infos">
                <span class="Repository-infos-language"><FontAwesomeIcon icon={faCode}/>&nbsp;{language}</span>
                <span class="Repository-infos-stars"><FontAwesomeIcon icon={faStar}/>&nbsp;{stargazers_count}</span>
                <span class="Repository-infos-stars"><FontAwesomeIcon icon={faCodeBranch}/>&nbsp;{forks}</span>
            </div>
        </article>
    )

}

export default Repository
